import { useState } from "react";
import Actual_Img from "./Viewer";
import LazyLoad from "react-lazyload";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css"; // Opcional: efecto de carga

export let exporter_showImg;

export default function Img_preview({ url, arr }) {
  const [actualimg, showImg] = useState("");
  exporter_showImg = showImg;

  // const preview_img = url;
  // const size_index  = url.indexOf("&width");
  // return img.substring(0, size_index) + "&height=240"

  const size_index = url.indexOf("&width");
  const preview_img = url.substring(0, size_index) + "&height=240";

  return (
    <>
      <div
        onTouchStart={() => {
          showImg(
            <Actual_Img
              del={showImg}
              index={arr.indexOf(url)}
              arr={arr}
            ></Actual_Img>
          );
        }}
        style={{
          display: "inline-block",
          width: "35%",
          marginLeft: "4%",
          marginTop: "2%",
          marginRight : "6%",
        }}
      >
        {/* <LazyLoadImage once style={{ display: "inline-block" }}>
            <img src={preview_img} style={{width: "100%" , display: "inline-block"}} alt="" />
            </LazyLoadImage> */}

        <LazyLoadImage
          effect="blur" // Efecto opcional de carga
          src={preview_img}
          style={{width: "100%" , display: "inline-block"}}
        />
      </div>

      {actualimg}
    </>
  );
}
