import { req_login } from "../middleware";
import Folders_Compo from "./Folders_Compo";
import { Capitalizate } from "../funcs/Case";
import folders from "../funcs/folders";
import { unzip, zip } from "../common/zip";
import SW from "../common/sw";

export default function Folders() {
  req_login();


  
  const profiles = localStorage.getItem("profiles")
    ? unzip(localStorage.getItem("profiles"))
    : { all: folders.map((ar) => ar.id) };


  

  const profile_main = () => {
    SW({
      title: "PROFILES",
      allowOutsideClick: false,
      html: `
          <div class="text-center">
              <button id="cre-pro" class="btn btn-success" style="font-size: 1.8rem; margin-top : 5%">CREATE PROFILE</button>
              <br />
              <button id="get-pro"  class="btn btn-success" style="font-size: 1.8rem; margin-top : 5%">GET PROFILES</button>
              <br />
              <button id="upt-pro"  class="btn btn-warning" style="font-size: 1.8rem; margin-top : 5%">UPDATE PROFILE</button>
              <br />
              <button  id="del-pro"  class="btn btn-danger" style="font-size: 1.8rem; margin-top : 5%">DELETE PROFILE</button>
              <br />
          </div>
              `,
    });

    document.getElementById("cre-pro").onclick = profile_cre_modal;
    document.getElementById("get-pro").onclick = profile_get_modal;
    document.getElementById("upt-pro").onclick = profile_upt_modal;
    document.getElementById("del-pro").onclick = profile_del_modal;
  };

  const profile_del_modal = () => {
    SW(
      {
        title: "PROFILES - DELETE",
        html: `
      <div>
        <ul id="list-profile"></ul>
      </div>
          `,
        allowOutsideClick: false,
        showConfirmButton: false,
        showCancelButton: true,
      },
      (result) => {
        if (result.isDismissed) {
          profile_main();
        }
      }
    );

    const container = document.getElementById("list-profile");

    Object.keys(profiles).map((k) => {
      const btn = document.createElement("button");
      btn.textContent = k;
      btn.className = "w-100 btn btn-info";
      container.append(btn);
      container.append(document.createElement("br"));
      btn.onclick = () => {
        let text = "THE PROFILE HAVE THIS CONTENT => \n";
        Object.keys(profiles).map((k) => {
          profiles[k].map((id) => {
            const data = folders.filter((img) => img.id == id)[0];

            text += `${data.id}-${data.name}`;
          });
        });
        /* eslint-disable */
        if (confirm(text + "\n Are you sure you want to eliminate it?")) {
          delete profiles[k];
          localStorage.setItem("profiles", zip(profiles));
        }
        /* eslint-enable */
      };
    });
  };

  const profile_upt_modal = () => {
    SW(
      {
        title: "PROFILES - UPDATE",
        html: `
             <div>
        <ul id="list-profile"></ul>
      </div>
        `,
        allowOutsideClick: false,
        showConfirmButton: false,
        showCancelButton: true,
      },
      (result) => {
        // if (result.isDismissed) {
        //   profile_main();
        // }
      }
    );

    const container = document.getElementById("list-profile");
    Object.keys(profiles).map((k) => {
      const btn = document.createElement("button");
      btn.textContent = k;
      btn.className = "w-100 btn btn-info";
      container.append(btn);
      container.append(document.createElement("br"));
      btn.onclick = () => {
        /* eslint-disable */
        // Swal.close()
        /* eslint-enable */
        SW({
          title: "PROFILES- " + k + " - UPDATE",
          html: `
              <div>
          <div id="container"></div>
          <button id="add_profile">+</button>
          </div>
  
          <button id="save">UPDATE</button>
            `,
          allowOutsideClick: false,
          showConfirmButton: false,
          showCancelButton: true,
        });

        document.getElementById("add_profile").className = "btn btn-info w-100";
        document.getElementById("add_profile").style.margin = "3% 0%";

        profiles[k].map((pro) => {
          const container = document.getElementById("container");
          const newprocontainer = document.createElement("div");
          const selector = document.createElement("select");
          selector.className = "prof-cre-sel";
          selector.value = k;
          selector.style.backgroundColor = "transparent";
          selector.style.color = "white";

          folders.map((im) => {
            const opt = document.createElement("option");
            opt.textContent = `${im.id}-${im.name}`;
            opt.value = im.id;
            selector.append(opt);
          });
          const delbtn = document.createElement("button");
          delbtn.textContent = "X";
          delbtn.onclick = () => {
            newprocontainer.remove();
          };

          selector.style.width = "80%";
          selector.style.marginRight = "5%";
          selector.style.fontSize = "1.5rem";

          delbtn.style.width = "15%";
          delbtn.style.fontSize = "1.3rem";
          delbtn.className = `btn btn-danger`;

          newprocontainer.style.marginBottom = "7%";

          container.append(newprocontainer);
          newprocontainer.append(selector);
          newprocontainer.append(delbtn);
          newprocontainer.append(document.createElement("br"));
        });

        document.getElementById("add_profile").onclick = () => {
          const container = document.getElementById("container");
          const newprocontainer = document.createElement("div");
          const selector = document.createElement("select");
          selector.className = "prof-cre-sel";

          selector.style.backgroundColor = "transparent";
          selector.style.color = "white";

          folders.map((im) => {
            const opt = document.createElement("option");
            opt.textContent = `${im.id}-${im.name}`;
            opt.value = im.id;
            selector.append(opt);
          });
          const delbtn = document.createElement("button");
          delbtn.textContent = "X";
          delbtn.onclick = () => {
            newprocontainer.remove();
          };

          selector.style.width = "80%";
          selector.style.marginRight = "5%";
          selector.style.fontSize = "1.5rem";

          delbtn.style.width = "15%";
          delbtn.style.fontSize = "1.3rem";
          delbtn.className = `btn btn-danger`;

          newprocontainer.style.marginBottom = "7%";

          container.append(newprocontainer);
          newprocontainer.append(selector);
          newprocontainer.append(delbtn);
          newprocontainer.append(document.createElement("br"));
        };

        document.getElementById("save").className = "btn btn-success w-100";
        document.getElementById("save").style.margin = "3% 0%";

        document.getElementById("add_profile").style.fontSize = "1.3rem";
        document.getElementById("save").style.fontSize = "1.3rem";

        document.getElementById("save").onclick = () => {
          if (window.confirm("Create new profile")) {
            const profile_inputs =
              document.getElementsByClassName("prof-cre-sel");
            const data = [];
            for (let i = 0; i < profile_inputs.length; i++) {
              data.push(profile_inputs[i].value);
            }

            profiles[k] = data;

            localStorage.setItem("profiles", zip(profiles));
          }
        };
      };
    });
  };
  const profile_get_modal = () => {
    SW(
      {
        title: "PROFILES - SHOW",
        html: `
      <div>
        <ul id="list-profile"></ul>
      </div>
          `,
        allowOutsideClick: false,
        showConfirmButton: false,
        showCancelButton: true,
      },
      (result) => {
        if (result.isDismissed) {
          profile_main();
        }
      }
    );

    const container = document.getElementById("list-profile");

    Object.keys(profiles).map((k) => {
      const btn = document.createElement("button");
      btn.textContent = k;
      btn.className = "w-100 btn btn-info";
      container.append(btn);
      container.append(document.createElement("br"));
      btn.onclick = () => {
        let text = "CONTENT OF THE PROFILE => \n";
        Object.keys(profiles).map((k) => {
          profiles[k].map((id) => {
            const data = folders.filter((img) => img.id == id)[0];

            text += `${data.id}-${data.name}`;
          });
        });

        alert(text);
      };
    });
    // Object.keys(profiles).map(k => {
    //   profiles[k].map(id => {
    //     const data = folders.filter(img => img.id == id)[0];

    //   })
    // })
  };

  const profile_cre_modal = () => {
    SW(
      {
        title: "PROFILES - CREATE",
        html: `
          <div>
          <input id="add-profile-name" type="text" placeholder="Profile name"/>
          <div id="container"></div>
          <button id="add_profile">+</button>
          </div>
  
          <button id="save">CREATE</button>
          `,
        showConfirmButton: false,
        showCancelButton: true,
        allowOutsideClick: false,
      },
      (result) => {
        if (result.isDismissed) {
          profile_main();
        }
      }
    );

    document.getElementById("add-profile-name").style.width = "100%";
    document.getElementById("add-profile-name").style.color = "white";
    document.getElementById("add-profile-name").style.backgroundColor =
      "transparent";

    document.getElementById("add-profile-name").style.marginRight = "5%";
    document.getElementById("add-profile-name").style.fontSize = "1.5rem";

    document.getElementById("add_profile").className = "btn btn-info w-100";
    document.getElementById("add_profile").style.margin = "3% 0%";
    document.getElementById("add_profile").onclick = () => {
      const container = document.getElementById("container");
      const newprocontainer = document.createElement("div");
      const selector = document.createElement("select");
      selector.className = "prof-cre-sel";

      selector.style.backgroundColor = "transparent";
      selector.style.color = "white";

      folders.map((im) => {
        const opt = document.createElement("option");
        opt.textContent = `${im.id}-${im.name}`;
        opt.value = im.id;
        selector.append(opt);
      });
      const delbtn = document.createElement("button");
      delbtn.textContent = "X";
      delbtn.onclick = () => {
        newprocontainer.remove();
      };

      selector.style.width = "80%";
      selector.style.marginRight = "5%";
      selector.style.fontSize = "1.5rem";

      delbtn.style.width = "15%";
      delbtn.style.fontSize = "1.3rem";
      delbtn.className = `btn btn-danger`;

      newprocontainer.style.marginBottom = "7%";

      container.append(newprocontainer);
      newprocontainer.append(selector);
      newprocontainer.append(delbtn);
      newprocontainer.append(document.createElement("br"));
    };

    document.getElementById("save").className = "btn btn-success w-100";
    document.getElementById("save").style.margin = "3% 0%";

    document.getElementById("add_profile").style.fontSize = "1.3rem";
    document.getElementById("save").style.fontSize = "1.3rem";

    document.getElementById("save").onclick = () => {
      if (window.confirm("Create new profile")) {
        const profile_inputs = document.getElementsByClassName("prof-cre-sel");
        const data = [];
        for (let i = 0; i < profile_inputs.length; i++) {
          data.push(profile_inputs[i].value);
        }

        profiles[document.getElementById("add-profile-name").value] = data;

        localStorage.setItem("profiles", zip(profiles));
      }
    };
  };

  return (
    <>
      {folders.map((comp) => (
        <Folders_Compo
          com={comp.data[0]}
          name={Capitalizate(comp.name)}
        ></Folders_Compo>
      ))}

      <div>
        <button onTouchEnd={profile_main}>PROFILES</button>
        <button
          onTouchEnd={() => {
            // http://localhost:3000/#/folder/p/?profiles=1

            SW({
              title: "PROFILES - SHOW",
              html: `
        <div>
          <ul id="list-profile"></ul>
        </div>
            `,
              allowOutsideClick: false,
              showConfirmButton: false,
              showCancelButton: true,
            });

            const container = document.getElementById("list-profile");

            Object.keys(profiles).map((k) => {
              const btn = document.createElement("button");
              btn.textContent = k;
              btn.className = "w-100 btn btn-info";
              container.append(btn);
              container.append(document.createElement("br"));
              btn.onclick = () => {
                /* eslint-disable */
                if (confirm("Are you sure you want to see " + k + " folder?")) {
                  const ids = profiles[k];
                  let query = "#/folder/p/?profiles=";
                  ids.map((id, i ) => {
                      query += id
                    if(i != ids.length -1 ){
                      query+= ","
                    }
                  })

                  window.location.href = query; 
                  window.location.reload()
                }
                /* eslint-enable */
              };
            });
          }}
        >
          LOAD PROFILE
        </button>
      </div>
    </>
  );
}
