import { json, useLocation, useParams } from "react-router-dom";
import folders from "../funcs/folders";
import SW from "../common/sw";
import Img_preview, { exporter_showImg } from "./Img_Preview";
import Actual_Img from "./Viewer";
import LazyLoad from "react-lazyload";
import { useEffect, useState } from "react";

export default function Folder() {
  const params = useParams().name;
  const location = useLocation();
  let imgs = [];
  let folder = folders.filter((fol) => fol.name == params)[0];
  if (params != "p") {
    imgs = folder.data;
  } else {
    const searchParams = new URLSearchParams(location.search);
    const profilesID = searchParams.get("profiles").split(",");

    imgs = [];
    profilesID.map((id) => {
      const found = folders.filter((fold) => fold.id == id)[0];
      if (found) {
        imgs = imgs.concat(found.data);
      }
    });
  }
  const [visibleImages, setVisibleImages] = useState({ imgs: [] });

  useEffect(() => {
    let interval;
    const add_imgs = () => {
      let counter = 0;
      interval = setInterval(() => {
        visibleImages.imgs.push(counter);
        counter++;

        if (imgs[counter] == undefined) {
          clearInterval(interval);
        } else {
          setVisibleImages(JSON.parse(JSON.stringify(visibleImages)));
        }
      }, 500);
    };

    add_imgs();

    // const addImagesWithDelay = () => {
    //   const interval = setInterval(() => {
    //     if (visibleImages.length < imgs.length) {
    //       const newVisibleImages = imgs.slice(0, visibleImages.length + 2);
    //       setVisibleImages(<Img_preview url={imgs[0]} arr={imgs}></Img_preview>);
    //     } else {
    //       clearInterval(interval); // Detiene el intervalo cuando todas las imágenes están visibles
    //     }
    //   }, 1000); // Intervalo de 1 segundo entre cada par de imágenes
    // };

    // addImagesWithDelay();

    // Limpia el intervalo cuando el componente se desmonta o se actualiza
    // return () => clearInterval(interval);
  }, []); // Se ejecuta solo una vez al montar el componente

  // const imgs = folder.data.map(img => {
  //     const size_index  = img.indexOf("&width");
  //     return img.substring(0, size_index) + "&height=240"
  // });

  // const size_index  = com.indexOf("&width");

  // let url = com.substring(0, size_index) + "&height=240";

  // useEffect(()=>{
  //   const gallery = document.getElementById("gallery");
  //   // console.log(gallery);

  // },[])

  // const show_elements = () => {
  //   setTimeout(() => {
  //     useElements(10)
  //     imgs.map((img, i) => (
  //       <LazyLoad once style={{ display: "inline-block" }}>
  //         <Img_preview url={img} arr={imgs}></Img_preview>
  //       </LazyLoad>
  //     ));
  //   }, 5000)
  // };

  return (
    <div>
      <div>
        <button
          onTouchEnd={() => {
            SW({
              title: "AUTO MODE",
              showCancelButton: true,
              showConfirmButton: false,
              allowOutsideClick: false,
              html: `
                     <div class="text-center">   
                <label  style="color: white" >START FROM...</label>
                <br />
                <input class="bg-transparent border border-ligth rounded-0 text-light" id="modal-input" type="number" value=1 />
                <br />
                <label  style="color: white" >DELAY...(s)</label>
                <br />
                <input class="bg-transparent border border-ligth rounded-0 text-light"  id="modal-delay" type="number" value=3 />
                            <br />
                <button class="btn btn-info mt-3"  id="random">RANDOM</button>
                <br />
                <button  class="btn btn-info mt-3" id="orden">ORDERNATED</button>
                </div>
                        `,
            });

            document.getElementById("random").ontouchend = (e) => {
              start(e.target.id);
            };
            document.getElementById("orden").ontouchend = (e) => {
              start(e.target.id);
            };

            const start = (id) => {
              const startFrom = document.getElementById("modal-input").value;
              const delay = parseFloat(
                document.getElementById("modal-delay").value
              );
              /* eslint-disable */
              Swal.close();
              /* eslint-enable */

              exporter_showImg(
                <Actual_Img
                  del={exporter_showImg}
                  index={startFrom - 1}
                  arr={imgs}
                  method={id}
                  method_params={{ delay }}
                ></Actual_Img>
              );
            };
          }}
          className="w-100 btn btn-info"
          style={{ marginTop: ".5%", marginBottom: "3%", color: "black" }}
        >
          AUTO MODE
        </button>
      </div>

      <div>
        <button
          className="w-100 btn btn-danger"
          style={{ marginTop: ".5%", marginBottom: "3%", color: "black" }}
          onTouchEnd={() => {
            window.location.href = "#/folders";
          }}
        >
          BACK
        </button>
      </div>

      <div>
        {visibleImages.imgs.map((img) => (
          <Img_preview url={imgs[img]} arr={imgs}></Img_preview>
        ))}
      </div>

      {/* {elements} */}

      {/* {show_elements()} */}

      <br />
    </div>
  );
}

{
  /* <img src="tu-imagen.jpg" alt="Descripción de la imagen" />
</LazyLoad> */
}
