export default function Folders_Compo({com, name}){

    const size_index  = com.indexOf("&width");

    let url = com.substring(0, size_index) + "&height=240";


    return <>
    <div 
    onTouchEnd={()=>{
       window.location.href = "#/folder/"+ name.toLowerCase();
    }}
    style={{display: "inline-block", width: "45%", marginLeft: "3%" , textAlign: "center", border: "2px solid white"}}>
    <img src={url} style={{width:"100%"}} alt="" />
    <h3 style={{color : "white"}}>{name}</h3>
    </div>
    </>
}