import { useEffect, useState } from "react";
import spiner from "../media/gif/spiner.gif";
import { random } from "../common/random";
let interval
export default function Actual_Img({ index, del, arr, method_params = {}, method = "basic" }) {


   


    const [spinner, spinnerSTATE] = useState(
        <img
            style={{ width: "35px", position: "fixed", bottom: "0px", right: "0%" }}
            src={spiner}
            alt=""
        />
    );

    const [i, setIndex] = useState(index);


    const load_spinner = () => {
        spinnerSTATE(<img
            style={{ width: "35px", position: "fixed", bottom: "0px", right: "0%" }}
            src={spiner}
            alt=""
        />)
    }

    const destroy_spinner = () => {
        spinnerSTATE("")
    }


    let preventClose = false;
    const touch_start = {};
    let moveimg = 0;



    const prepare_image =(url)=>{
        const size_index  = url.indexOf("&width");
        return url.substring(0, size_index) + "&width=720";
    }


    const next_img =()=>{
        switch (method) {
            case "random":
        

                let breaker = 0;

                do {
                    const randomn = random(arr.length -1 );
                    if(breaker >= 10*100*100 || randomn != i){
                        setIndex(randomn)
                        load_spinner()
                        break
                    }
                    breaker++
                } while (true);
   
                break;
    
            case "orden":

                    if (arr[i + 1] != undefined) {
                        setIndex(i + 1)
                        load_spinner()
                    }else{
                        setIndex(0)
                        load_spinner()
                    }
                break;
        }
    
    }


    return (
        <div
            style={{
                position: "fixed",
                backgroundColor: "#0000005a",
                width: "100%",
                height: "100%",
                top: "0%",
                left: "0px",

                textAlign: "center",
                zIndex: "1000",
            }}
   
            onTouchStart={(e) => {
                touch_start.x = e.touches[0].clientX;
            }}
            onTouchMove={(e) => {
                if (method == "basic") {
                    const movement = {
                        x: e.touches[0].clientX - touch_start.x,
                    };

                    if (Math.abs(movement.x) > 35) {
                        preventClose = true;
                        if (movement.x > 0) {
                            moveimg = -1;
                        } else {
                            moveimg = 1;
                        }
                    }
                }
            }}
            onTouchEnd={() => {
                if (!preventClose) {
                    del("");
                    clearInterval(interval)
                } else {
                    if (arr[i + moveimg] != undefined) {
                        setIndex(i + moveimg)
                        load_spinner()
                    }


                }

                preventClose = false;
            }}
        >
            <img
                id="img"
                src={""}
                style={{
                    width:(window.innerWidth > window.innerHeight) ?("auto") : ("100%") ,
                    height: (window.innerWidth > window.innerHeight) ?("100%") : ("auto"),
                    opacity: 0,
                    marginTop: "5%",
                    transition: "opacity .5s"
                }}
                alt=""
            />



            <img
                id="preload"
                style={{
                    width: "0px",
                    height: "0px",
                    opacity: 0,
                    position: "absolute",
                    top: 0,
                    left: 0,
                }}
                onLoad={(e) => {
                    document.getElementById("img").style.opacity = 0;
                    setTimeout(() => {
                        document.getElementById("img").src = e.target.src;
                    }, 100);
                    setTimeout(() => {
                        document.getElementById("img").style.opacity = 1;
                        destroy_spinner()

                        setTimeout(() => {
                            next_img() 
                        },(method_params.delay + 2) * 1000 );

                    }, 600);
                }}
                src={prepare_image(arr[i])}
                alt=""
            />
            {spinner}
            {/* <img style={{width: "35px", position: "fixed", bottom: "0px", right:  "0%"}} src={spiner} alt="" /> */}
        </div>
    );
}
